.search-btn {
    cursor: pointer;
}

.play-btn {
    display: flex;
    width: 56px;
    height: 56px;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    background-color: rgba(75, 74, 74, 0.6);
    border-radius: 100px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    background: rgba(75, 74, 74, 0.6);
    backdrop-filter: blur(2px);
}

/*Scroll to top*/
.btn-scroll-top {
    position: fixed;
    right: 1.5rem;
    bottom: 1.5rem;
    width: 50px;
    height: 50px;
    cursor: pointer;
    display: block;
    border-radius: 50px;
    z-index: 1010;
    opacity: 0;
    box-shadow: 0 0 0 0.25rem rgba(var(--tc-neutral-900), 0.5);
    visibility: hidden;
    transform: translateY(0.75rem);
    transition:
        all 0.2s linear,
        margin-right 0s;
    background-color: #0e0e0f;

    &:after {
        position: absolute;
        content: '';
        text-align: center;
        line-height: 50px;
        background: url('../imgs/template/icons/arrow-up.svg') no-repeat center center;
        left: 0;
        top: 0;
        height: 50px;
        width: 50px;
        cursor: pointer;
        display: block;
        z-index: 1;
        transition: all 0.2s linear;
    }

    &.active-progress {
        visibility: visible;
        transform: translateY(0);

        @media (min-width: 320px) {
            opacity: 1;
        }
    }

    & svg path {
        fill: none;
    }

    & svg.progress-square path {
        stroke: var(--tc-neutral-400);
        stroke-width: 2;
        box-sizing: border-box;
        transition: all 0.2s linear;
    }
}

.btn {
    --tc-btn-padding-x: 56px;
    --tc-btn-padding-y: 20px;
    --tc-btn-font-weight: 600;
    --tc-btn-line-height: 1.5;
    --tc-btn-border-radius: 50px;
    --tc-btn-color: var(--tc-system-white);
    --tc-btn-bg: transparent;
    --tc-btn-border-color: transparent;
    --tc-btn-hover-border-color: transparent;
    padding: var(--tc-btn-padding-y) var(--tc-btn-padding-x);
    font-family: 'Geist', sans-serif;
    font-size: 1rem;
    font-weight: var(--tc-btn-font-weight);
    line-height: var(--tc-btn-line-height);
    color: var(--tc-btn-color);
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    border: 1px solid var(--tc-btn-border-color);
    border-radius: var(--tc-btn-border-radius);
    background-color: var(--tc-btn-bg);
    transition:
        color 0.15s ease-in-out,
        background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out,
        box-shadow 0.15s ease-in-out;
}

.btn-sm {
    --tc-btn-padding-x: 11px;
    --tc-btn-padding-y: 2px;
    --tc-btn-font-weight: 500;
    --tc-btn-line-height: 1.5;
    --tc-btn-border-radius: 4px;
}

.btn-dark {
    --tc-btn-color: var(--tc-system-white);
    --tc-btn-bg: var(--tc-system-dark);
    --tc-btn-border-color: transparent;
    --tc-btn-hover-color: var(--tc-system-white);
    --tc-btn-hover-bg: var(--tc-system-dark);
    --tc-btn-hover-border-color: transparent;
    --tc-btn-focus-shadow-rgb: 49, 132, 253;
    --tc-btn-active-color: var(--tc-neutral-0);
    --tc-btn-active-bg: var(--tc-system-dark);
    --tc-btn-active-border-color: var(--tc-system-dark);
    --tc-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --tc-btn-disabled-color: var(--tc-neutral-0);
    --tc-btn-disabled-bg: var(--tc-system-dark);
    --tc-btn-disabled-border-color: var(--tc-system-dark);
}

/* Link Effect 1 Styles */
.link-effect-1 {
    position: relative;
    display: inline-block;
    overflow: hidden;
    transition: all 0.4s ease;

    span.text-1 {
        position: relative;
        display: inline-block;
        transition: transform 0.4s ease;
    }

    span.text-2 {
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(100%);
        transition: transform 0.4s ease;
    }

    &:hover {
        span.text-1 {
            transform: translateY(-100%);
        }

        span.text-2 {
            transform: translateY(0);
        }
    }
}

/* Link Effect 2 Styles */
.link-effect-2 {
    position: relative;
    display: inline-flex;
    align-items: center;
    gap: 8px;
    transition: all 0.4s ease;

    .text {
        position: relative;
        display: inline-block;
        overflow: hidden;
        transition: transform 0.4s ease;
    }

    .text1 {
        position: relative;
        display: inline-block;
        transition: transform 0.4s ease;
    }

    .text2 {
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(100%);
        transition: transform 0.4s ease;
    }

    &:hover {
        .text1 {
            transform: translateY(-100%);
        }

        .text2 {
            transform: translateY(0);
        }
    }
}

/* Button Effect 1 Styles */
.button-effect-1 {
    position: relative;
    display: inline-block;
    transition: all 0.4s ease;
    line-height: 1.05;

    .btn-text {
        position: relative;
        display: inline-block;
        overflow: hidden;
        transition: transform 0.4s ease;
    }

    .btn-text-1 {
        position: relative;
        display: inline-block;
        transition: transform 0.4s ease;
    }

    .btn-text-2 {
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(100%);
        transition: transform 0.4s ease;
    }

    &:hover {
        .btn-text-1 {
            transform: translateY(-100%);
        }

        .btn-text-2 {
            transform: translateY(0);
        }
    }
}

/* Button hover effect */
.view-more {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    vertical-align: middle;
    text-decoration: none;
    background: transparent;
    padding: 0;
    font-size: inherit;
    font-family: inherit;
    width: 10rem;
    height: auto;

    .circle {
        transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
        position: relative;
        display: block;
        margin: 0;
        width: 3rem;
        height: 3rem;
        background-color: var(--tc-neutral-900);
        border-radius: 1.625rem;

        .icon {
            transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
            position: absolute;
            top: 0;
            bottom: 0;
            margin: auto;
            background-color: var(--tc-system-white);

            &.arrow {
                transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
                left: 0.625rem;
                width: 1.125rem;
                height: 0.125rem;
                background: transparent;

                &::before {
                    position: absolute;
                    content: '';
                    top: -0.25rem;
                    right: 0.0625rem;
                    width: 0.625rem;
                    height: 0.625rem;
                    border-top: 0.125rem solid var(--tc-system-white);
                    border-right: 0.125rem solid var(--tc-system-white);
                    transform: rotate(45deg);
                }
            }
        }
    }

    .button-text {
        transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0.75rem 0;
        margin: 0 0 0 1.85rem;
        color: var(--tc-neutral-600);
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        line-height: 150%;
    }

    &.white {
        .circle {
            background-color: var(--tc-system-white);

            .icon {
                &.arrow {
                    &::before {
                        border-color: var(--tc-neutral-900);
                    }
                }
            }
        }

        .button-text {
            color: var(--tc-system-white);
        }

        &:hover {
            .circle {
                background-color: var(--tc-system-white);

                .icon.arrow {
                    background-color: var(--tc-neutral-900);

                    &::before {
                        border-color: var(--tc-neutral-900);
                    }
                }
            }

            .button-text {
                color: var(--tc-neutral-900);
            }
        }
    }

    &.w-12rem {
        width: 12rem;
    }

    &:hover {
        .circle {
            width: 100%;
            background-color: var(--tc-neutral-900);

            .icon.arrow {
                background-color: var(--tc-system-white);
                transform: translate(1rem, 0);
            }
        }

        .button-text {
            color: var(--tc-system-white);
        }
    }
}

