// Swiper customize
.swiper-btn-next,
.swiper-btn-prev {
    display: flex;
    width: 54px;
    height: 54px;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    border: 1px solid var(--tc-neutral-100);
    background-color: var(--tc-system-white);
    transition: all 0.3s ease-in-out;

    svg {
        width: 24px;
        height: 24px;

        path {
            stroke: var(--tc-neutral-900);
            transition: all 0.3s ease-in-out;
        }
    }

    &::after {
        content: '';
    }

    &:hover,
    :active {
        border-color: var(--tc-neutral-900);
        background-color: var(--tc-neutral-900);

        svg {
            path {
                stroke: var(--tc-system-white);
            }
        }
    }
}

.swiper-pagination {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 5px 8px;
    align-items: center;
    left: 50% !important;
    width: unset !important;
    gap: 5px;
    border-radius: 100px;
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(1px);
    transform: translateX(-50%);

    .swiper-pagination-bullet {
        width: 8px;
        height: 8px;
        text-align: center;
        line-height: 8px;
        font-size: 12px;
        color: var(--tc-system-dark);
        opacity: 1;
        background-color: var(--tc-system-white);
    }

    .swiper-pagination-bullet-active {
        color: var(--tc-system-dark);
        background-color: var(--tc-neutral-900);
    }
}

.hover-up {
    transition: all 0.3s ease-in-out;

    &:hover {
        transform: translateY(-3px);
        transition-duration: 0.3s;
    }
}

// Popup search
.popup-search-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 200;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}

.popup-search {
    position: absolute;
    top: 143px;
    left: 50%;
    opacity: 0;
    overflow-y: auto;
    visibility: hidden;
    transform: translateX(-50%);
    // width: 100%;
    z-index: 201;
    transition: all 0.3s ease;

    &.show {
        opacity: 1;
        visibility: visible;
    }

    .close-popup {
        cursor: pointer;
        transition: transform 0.3s ease;
        display: inline-block;
        width: 24px;
        height: 24px;

        &:hover {
            transform: rotate(90deg);
        }
    }

    .popup-search-content {
        background-color: var(--tc-neutral-50);
        border-radius: 8px;
        z-index: 999;
        padding: 45px 54px;

        .block-tag {
            display: flex;
            flex-wrap: wrap;
            gap: 10px;
        }
    }
}

