.hover-underline {
    position: relative;
    text-decoration: none;
    display: inline-block;

    h4 {
        position: relative;
        transition: all 0.3s ease;
        margin: 0;
        display: inline;
    }

    .line-wrapper {
        position: relative;
        display: inline;

        &::after {
            content: '';
            position: absolute;
            bottom: 2px;
            left: 0;
            right: 0;
            height: 2px;
            background: linear-gradient(90deg, #EE6E47 0%, #28886A 100%);
            transform: scaleX(0);
            transform-origin: left;
            transition: transform 0.3s ease;
        }
    }

    &:hover {
        h4 {
            transform: translateY(-1px);
        }

        .line-wrapper {
            &::after {
                transform: scaleX(1);
                transform-origin: left;
            }
        }
    }

    .line-wrapper {
        &::after {
            transform-origin: right;
        }
    }
}

// CarouselTicker custom styles
.carouselTicker {
