            background-color: var(--tc-system-white);
            color: var(--tc-neutral-900);
            text-decoration: none;
            white-space: nowrap;
            transition: all 0.3s ease-in-out;

            &:hover {
                .number {
                    color: var(--tc-system-dark);
                }
