//404 img effect
.imgs-404 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 200px;
    margin-bottom: 30px;

    img {
        position: absolute;
        opacity: 0;
        max-width: 150px;
        height: auto;
        animation: fadeInOut 6s infinite ease-in-out;

        &.img-1 {
            animation-delay: 0s;
        }

        &.img-2 {
            animation-delay: 2s;
        }

        &.img-3 {
            animation-delay: 4s;
        }
    }
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
    }

    5% {
        opacity: 1;
    }

    30% {
        opacity: 1;
    }

    35% {
        opacity: 0;
    }

    100% {
        opacity: 0;
    }
}


