// card article
.article {
    &:hover {
        .arrow-box {
            border-color: var(--tc-neutral-900);
            background-color: var(--tc-neutral-900);
            transform: rotate(-45deg);

            svg {
                path {
                    stroke: var(--tc-system-white);
                }
            }
        }
    }

    .badge {
        display: inline-flex;
        padding: 7px 14px;
        justify-content: center;
        align-items: center;
        font-weight: 500;
        gap: 10px;
        border-radius: 25px;
    }

    .book-mark {
        position: absolute;
        top: 20px;
        right: 20px;
        width: 28px;
        height: 28px;
        border-radius: 4px;
        border: 1px solid var(--tc-neutral-200);
        display: flex;
        justify-content: center;
        align-items: center;

        @media (min-width: 768px) {
            top: 34px;
            right: 34px;
        }

        &.active {
            background-color: var(--tc-neutral-900);
            border-color: var(--tc-neutral-900);

            svg {
                path {
                    stroke: var(--tc-neutral-100);
                }
            }
        }
    }

    .comment {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 4px;
        color: var(--tc-neutral-600);

        &:hover {
            color: var(--tc-neutral-900);
        }
    }

    .readers {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 4px;
        color: var(--tc-neutral-600);

        &:hover {
            color: var(--tc-neutral-900);
        }
    }

    &.card-1 {
        .card-img-top {
            border-radius: 16px;
            overflow: hidden;
            display: block;

            img {
                overflow: hidden;
            }
        }

        .card-body {
            border: 1px solid var(--tc-neutral-200);
            border-radius: 20px;
            display: flex;
            padding: 25px;
            justify-content: space-between;
            background-color: rgba(255, 255, 255, 0.6);
            margin: -70px 18px 0;
            gap: 10px;
            backdrop-filter: blur(15px);

            @media (min-width: 992px) {
                margin: -100px 26px 0;
                padding: 40px;
                gap: 50px;
            }

            .book-mark {
                position: relative;
                top: unset;
                right: unset;
                border-color: var(--tc-system-white);

                @media (min-width: 992px) {}
            }

            .right {
                display: flex;
                flex-direction: column;
                gap: 30px;
            }
        }
    }

    &.card-2 {
        .post-link {
            display: flex;
            flex-direction: column;
            gap: 8px;
        }

        .card-img-top {
            img {
                border-radius: 16px;
                overflow: hidden;
            }
        }

        @media (min-width: 768px) {
            .card-corner {
                border: none;

                .curve-one {
                    top: -16px;
                    right: 0px;
                    border: none;
                }

                .curve-two {
                    bottom: 0px;
                    left: -16px;
                    border: none;
                }
            }
        }

        .card-body {
            display: inline-flex;
            padding: 24px 33px;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            gap: 3px;
            border-radius: 16px;
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);

            /* bg-blur-1 */
            @media (min-width: 768px) {
                max-width: 70%;
                position: absolute;
                bottom: 32px;
                left: 32px;
                background: rgba(255, 255, 255, 0.6);
                backdrop-filter: blur(15px);
            }
        }
    }

    &.card-3 {
        gap: 8px;
        align-items: stretch;

        .card-img-top {
            min-width: 225px;
            border-radius: 16px;
            overflow: hidden;
            display: block;

            img {

                overflow: hidden;
                object-fit: cover;
            }
        }

        .card-body {
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);
            backdrop-filter: blur(15px);
            padding: 30px 26px;
            border-radius: 20px;
            display: flex;
            justify-content: space-between;
            gap: 50px;

            .bottom {
                display: flex;
                gap: 27px;
            }
        }
    }

    &.card-4 {
        .card-body {
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);
            backdrop-filter: blur(15px);
            padding: 40px 34px 25px 34px;
            border-radius: 20px;
            display: flex;
            justify-content: space-between;
            gap: 50px;

            @media (min-width: 768px) {
                padding: 60px 54px 35px 54px;
            }
        }
    }

    &.card-5 {
        .badge {
            position: absolute;
            bottom: 15px;
            left: 15px;
        }

        .thumbnail {
            min-height: 229px;
            max-height: 229px;
        }

        .card-corner {
            &.white {
                background-color: var(--tc-system-white);

                &::before {
                    background-color: var(--tc-system-white);
                }

                .curve-one {
                    box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-system-white);
                }

                .curve-two {
                    box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-system-white);
                }
            }
        }
    }

    &.card-6 {
        display: flex;
        align-items: center;
        gap: 18px;
        padding: 14px 16px;
        border-radius: 16px;
        border: 1px solid var(--tc-neutral-200);
        transition: all 0.3s ease-in-out;

        .card-body {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 8px;
        }

        .thumbnail {
            max-width: 108px;
            max-height: 83px;

            img {
                border-radius: 16px;
                width: 100%;
                height: 100%;
                transition: all 0.3s ease-in-out;
            }
        }

        .comment {
            align-self: flex-end;
        }

        &:hover {
            border-color: var(--tc-neutral-500);
            background-color: var(--tc-system-white);

            .thumbnail {
                img {
                    transform: scale(1.05) rotate(1deg);
                    transition: all 0.3s ease-in-out;
                }
            }
        }
    }

    &.card-7 {
        .card-img {
            min-height: 206px;
        }

        .card-body {
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);
            backdrop-filter: blur(15px);
            padding: 30px;
            border-radius: 20px;
            display: flex;
            justify-content: space-between;
            gap: 50px;

            .book-mark {
                top: 20px;
                right: 20px;
            }
        }
    }

    &.card-9 {
        gap: 8px;

        .card-img-top {
            max-height: 286px;
            width: 100%;
            overflow: hidden;
            border-radius: 16px;

            @media (min-width: 768px) {
                max-width: 260px;
                min-width: 260px;
                overflow: hidden;
            }
        }

        .card-body {
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);
            backdrop-filter: blur(15px);
            border-radius: 20px;
            padding: 30px 70px 20px 25px;
            display: flex;
            justify-content: space-between;
        }
    }

    &.card-10 {
        &.style-1 {
            display: flex;
            gap: 10px;
            align-items: center;
            max-width: 350px;
        }

        &.style-2 {
            flex-direction: row-reverse;
            max-width: 350px;
            display: flex;
            padding: 14px 18px;
            gap: 10px;
            align-items: center;
            align-self: stretch;
            border-radius: 16px;
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);
        }

        .card-img {
            min-width: 108px;
            max-width: 108px;
            min-height: 83px;
            display: inline-grid;

            img {
                border-radius: 10px;
                overflow: hidden;
                height: 100%;
            }
        }
    }

    &.card-11 {
        display: flex;
        padding-bottom: 7px;
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;

        .thumbnail {
            max-height: 340px;

            .badge {
                position: absolute;
                bottom: 15px;
                left: 15px;
            }
        }

        .card-body {
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);
            backdrop-filter: blur(15px);
            padding: 45px 25px 30px;
            border-radius: 20px;
            display: flex;
            justify-content: space-between;

            @media (min-width: 992px) {
                padding: 45px 50px 30px;
            }

            .card-corner {
                border-top-left-radius: 16px;

                .curve-one {
                    border-bottom-right-radius: 16px;
                    border-bottom: 1px solid var(--tc-neutral-200);
                }

                .curve-two {
                    border-bottom-right-radius: 16px;
                    border-right: 1px solid var(--tc-neutral-200);
                }
            }
        }
    }

    &.card-12 {
        gap: 15px;

        .card-img-top {
            min-width: 225px;
            max-height: 370px;
            width: 100%;

            @media (min-width: 768px) {
                max-height: 100%;
            }

            @media (min-width: 992px) {
                max-width: 380px;
            }
        }

        .card-body {
            border: 1px solid var(--tc-neutral-200);
            background-color: var(--tc-system-white);
            backdrop-filter: blur(15px);
            padding: 45px 25px 30px;
            border-radius: 20px;
            display: flex;
            justify-content: space-between;

            @media (min-width: 992px) {
                padding: 50px 50px 30px;
            }
        }
    }
}

.post-link {
    display: grid;
    position: relative;
}

.thumbnail {
    position: relative;
    border-radius: 1em;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.card-corner {
    z-index: 2;
    background-color: var(--tc-neutral-50);
    border-top-left-radius: 2em;
    width: 58px;
    height: 58px;
    position: absolute;
    inset: auto 0% 0% auto;
    border-top: 1px solid var(--tc-neutral-200);
    border-left: 1px solid var(--tc-neutral-200);

    &::before {
        content: '';
        position: absolute;
        border-top-left-radius: 2.5em;
        width: 120%;
        height: 120%;
        z-index: 1;
        background-color: var(--tc-neutral-50);
    }

    &.no-border {
        border: none;

        .curve-one {
            top: -16px;
            right: 0px;
            border: none;
        }

        .curve-two {
            bottom: 0px;
            left: -16px;
            border: none;
        }
    }
}

.card-corner-2 {
    z-index: 2;
    background-color: var(--tc-neutral-50);
    border-bottom-right-radius: 2em;
    width: 58px;
    height: 58px;
    position: absolute;
    inset: 0% auto auto 0%;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 58px;
        background: transparent;
        width: 16px;
        height: 16px;
        border-bottom-right-radius: 16px;
        box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-neutral-50);
        rotate: 180deg;
    }

    &::after {
        content: '';
        position: absolute;
        top: 58px;
        left: 0;
        background: transparent;
        width: 16px;
        height: 16px;
        border-bottom-right-radius: 16px;
        box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-neutral-50);
        rotate: 180deg;
    }
}

.arrow-box {
    z-index: 4;
    display: flex;
    width: 46px;
    height: 46px;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    border: 1px solid var(--tc-neutral-200);
    background-color: var(--tc-system-white);
    position: absolute;
    inset: 8px 0% 0% 8px;
    overflow: hidden;
    transition: all 0.3s ease-in-out;

    svg {
        path {
            stroke: var(--tc-neutral-900);
            transition: all 0.3s ease-in-out;
        }
    }
}

.curve-one {
    position: absolute;
    content: '';
    top: -16px;
    right: -1px;
    background: transparent;
    width: 16px;
    height: 16px;
    border-bottom-right-radius: 16px;
    box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-neutral-50);
    border-right: 1px solid var(--tc-neutral-200);
    border-bottom: 1px solid var(--tc-neutral-200);
}

.curve-two {
    position: absolute;
    content: '';
    bottom: -1px;
    left: -16px;
    background: transparent;
    width: 16px;
    height: 16px;
    border-bottom-right-radius: 16px;
    box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-neutral-50);
    border-bottom: 1px solid var(--tc-neutral-200);
    border-right: 1px solid var(--tc-neutral-200);
}

//  card author

.author-grid-wrap {
    position: relative;

    img {
        border-radius: 1rem;
        overflow: hidden;
    }

    &.neutral-50 {
        .author-sticky-block-left-bottom {
            background-color: var(--tc-neutral-50);

            .author-sticky-corner-left-top {
                box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-neutral-50);
            }

            .author-sticky-corner-right-bottom {
                box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-neutral-50);
            }
        }
    }
}

.bg-50 {

    .author-sticky-block-left-bottom {
        background-color: var(--tc-neutral-50);
    }

    .author-sticky-corner-left-top,
    .author-sticky-corner-right-bottom {
        box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-neutral-50);
    }
}

.author-sticky-block-left-bottom {
    height: 90px;
    padding-top: 15px;
    z-index: 2;
    grid-column-gap: 3px;
    grid-row-gap: 3px;
    background-color: var(--tc-bg-1);
    border-top-right-radius: 16px;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 136px;
    height: 52px;
    padding-top: 10px;
    display: flex;
    position: absolute;
    inset: auto auto 0% 0%;
}

.author-sticky-corner-left-top {
    position: absolute;
    content: '';
    bottom: 52px;
    left: 0px;
    transform: rotate(90deg);
    background: transparent;
    width: 16px;
    height: 16px;
    border-bottom-right-radius: 16px;
    box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-bg-1);
}

.author-sticky-corner-right-bottom {
    position: absolute;
    content: '';
    bottom: 0px;
    left: 136px;
    transform: rotate(90deg);
    background: transparent;
    width: 16px;
    height: 16px;
    border-bottom-right-radius: 16px;
    box-shadow: 0.313rem 0.313rem 0 0.313rem var(--tc-bg-1);
}

// card category
.category-card {
    &.style-1 {
        display: flex;
        gap: 18px;
        min-height: 104px;
        height: 104px;
        padding-right: 55.229px;
        align-items: center;
        border-radius: 12px;
        overflow: hidden;
        border: 1px solid var(--tc-neutral-200);
        background-color: var(--tc-system-white);

        .thumbnail {
            position: relative;
            min-width: 104px;
            max-width: 104px;
            width: 100%;
            height: 100%;
            border-radius: 0;

            img {
                width: 100%;
                height: 100%;
            }
        }

        .post-content {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            gap: 10px;

            .post-count {
                background-color: var(--tc-neutral-100);
                padding: 3px 8px;
                display: inline;
                border-radius: 8px;
            }
        }

        &:hover {
            img {
                transform: scale(1.05) rotate(1deg);
                transition: all 0.3s ease-in-out;
            }
        }
    }

    &.style-2 {
        // max-width: 185px;
        // width: 185px;
        height: 71px;
        min-height: 71px;
        border-radius: 12px;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 0;
            opacity: 0.68;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.35) 0%, #000 100%);
        }

        .post-content {
            z-index: 2;

            h6 {
                color: var(--tc-system-white);
                text-align: center;
            }

            .post-count {
                background: rgba(255, 255, 255, 0.3);
                padding: 3px 8px;
                display: inline;
                border-radius: 8px;
                color: #fff;
            }
        }
    }
}

.card-recommend {
    img {
        max-height: 280px;
    }
}

.hover-effect-1 {
    overflow: hidden;
}

.col-lg-4 .article.card-10.style-2 {
    width: 376px;
    max-width: 398px;
}

.col-lg-4 .article.card-10.style-2 .card-body {
    width: 250px;
}

@media screen and (max-width: 992px) {
    .col-lg-4 .article.card-10.style-2 {
        width: 350px;
        max-width: 350px;
    }

    .col-lg-4 .article.card-10.style-2 .card-body {
        width: 100%;
    }
}

.card-swiper-thumb {
    cursor: pointer;
}