// sidebar
.sidebar-left {
    max-width: 460px;
    width: 460px;
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: var(--tc-neutral-50);
    padding: 0 50px;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    overflow-y: auto;
    z-index: 101;

    &.active {
        transform: translateX(0);
    }

    .card-corner {
        background-color: var(--tc-neutral-50);

        .curve-one,
        .curve-two {
            svg {
                path {
                    fill: var(--tc-neutral-50);
                }
            }
        }
    }

    .header-sidebar {
        position: sticky;
        top: 0;
        padding: 20px 0;
        z-index: 10;
        border-bottom: 1px solid var(--tc-neutral-200);
        background-color: var(--tc-neutral-50);
    }

    .sidebar-nav {
        display: flex;
        flex-direction: column;
        // gap: 12px;
        padding: 15px 0;

        .collapse-menu,
        .sub-menu {
            max-height: 0;
            overflow: hidden;
            transition: max-height 0.3s ease-in-out;
            margin: 0;
            padding-left: 15px;

            li {
                opacity: 0;
                transition: opacity 0.3s ease-in-out;
            }
        }

        .nav-item.active,
        .nav-item-has-child.active {
            >.nav-link {
                &:after {
                    transform: translateY(-50%) rotate(180deg);
                }
            }

            >.collapse-menu,
            >.sub-menu {
                max-height: 2000px;

                >li {
                    opacity: 1;
                    transition-delay: 0.2s;
                }
            }
        }

        .nav-item {
            position: relative;
            padding: 10px 0;

            &.active {
                >.nav-link {
                    &:after {
                        transform: translateY(-50%) rotate(180deg);
                    }
                }

                >.collapse-menu {
                    max-height: 1000px;
                    opacity: 1;
                    visibility: visible;
                    padding: 10px 0 5px 15px;
                }
            }

            .nav-link {
                position: relative;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: var(--tc-neutral-900);
                font-weight: 500;
                transition: color 0.3s ease;

                &:after {
                    content: '';
                    position: absolute;
                    background-image: url(../../assets/imgs/template/icons/arrow-down.svg);
                    background-size: contain;
                    background-repeat: no-repeat;
                    width: 18px;
                    height: 18px;
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    transition: all 0.3s ease;
                    opacity: 0.7;
                }

                &.collapsed:after {
                    transform: translateY(-50%) rotate(-90deg);
                }
            }

            .collapse-menu {
                opacity: 0;
                // visibility: hidden;
                transition: all 0.3s ease;
                margin: 0;
                padding-left: 15px;

                .nav-item {
                    margin-bottom: 5px;

                    .nav-link {
                        font-size: 14px;
                        padding: 8px 0;

                        &:after {
                            width: 12px;
                            height: 12px;
                            right: 5px;
                        }
                    }

                    .sub-menu {
                        opacity: 0;
                        visibility: hidden;
                        transition: all 0.3s ease;
                        padding-left: 15px;
                        margin: 0;

                        .collapse-item {
                            display: block;
                            padding: 6px 0;
                            color: var(--tc-neutral-700);
                            font-size: 13px;
                            transition: color 0.2s ease;
                        }
                    }

                    &.show-submenu {
                        >.nav-link:after {
                            transform: translateY(-50%) rotate(180deg);
                        }

                        >.sub-menu {
                            max-height: 1000px;
                            opacity: 1;
                            visibility: visible;
                            padding: 5px 0 5px 15px;
                        }
                    }
                }

                .collapse-item {
                    display: block;
                    padding: 5px 0;
                    color: var(--tc-neutral-800);
                    font-size: 16px;
                    transition: color 0.2s ease;
                }
            }
        }
    }

    .sidebar-footer {
        margin-top: 60px;
        margin-bottom: 30px;
        display: flex;
        padding: 30px;
        flex-direction: column;
        align-items: flex-start;
        gap: 27px;
        flex-shrink: 0;
        border-radius: 16px;
        border: 1px solid var(--tc-neutral-200);
        background-color: var(--tc-system-white);

        .form-control {
            max-width: 275px;
        }
    }
}

.sidebar-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 100;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}