// Font Family
@import url('https://fonts.googleapis.com/css2?family=Geist:wght@100..900&display=swap');

$font-family: (
    body: (font-family: 'Geist',
    ),
    heading: (font-family: 'Geist',
    ),
);
// Font Size
$font-size: (
    body: (font-size: 16px,
    ),
    fs: (1: 40px,
        2: 32px,
        3: 28px,
        4: 24px,
        5: 20px,
        6: 16px,
        7: 14px,
        8: 12px,
    ),
    ds: (1: 80px,
        2: 72px,
        3: 64px,
        4: 56px,
        5: 48px,
        6: 40px,
    ),
    ds-xs: (1: 64px,
        2: 56px,
        3: 48px,
        4: 36px,
        5: 28px,
        6: 24px,
    ),
);
// Line Height
$theme-lh: (
    body: (line-height: 1.5,
    ),
    heading: (line-height: 1.2,
    ),
);
// Font Weight
$font-weight: (
    fw: (black: 900,
        extra-bold: 800,
        bold: 700,
        semi-bold: 600,
        medium: 500,
        regular: 400,
        light: 300,
    ),
);