// $variable-prefix: tc- !default;
$prefix: tc- !default;

// user-variables
/*-----------------------------------------------------------------
 --------------- START CUSTOMIZE ----------------------------
-----------------------------------------------------------------*/
*,
::after,
::before {
    box-sizing: border-box;
}

@media (min-width: 1400px) {

    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl,
    .container-xxl {
        max-width: 1195px;
    }
}

// Text Selection Color
::selection {
    background-color: var(--tc-neutral-900);
    color: var(--tc-system-white);
}

::-moz-selection {
    background-color: var(--tc-neutral-900);
    color: var(--tc-system-white);
}

// bold and string font color
b,
strong {
    font-weight: bolder;
    color: var(--tc-neutral-800);
}

// blockquote design
.blockquote {
    background-color: var(--tc-neutral-100);
    padding: 100px 40px 40px 40px;
    border-radius: 8px;
    position: relative;
    margin: 20px auto;

    &:before {
        left: 40px;
        top: 40px;
        width: 32px;
        height: 32px;
        display: block;
        background-image: url('../imgs/template/icons/quote.svg');
        content: '';
        position: absolute;
        color: var(--tc-neutral-500);
        font-size: 32px;
    }

    @media (min-width: 768px) {
        padding: 40px 40px 40px 110px;
    }
}

.form-control {
    display: inline-block;
    width: 100%;
    padding: 17px 30px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: var(--tc-neutral-600);
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: var(--tc-system-white);
    background-clip: padding-box;
    border: 1px solid var(--tc-neutral-500);
    border-radius: 50px;
    transition:
        border-color 0.15s ease-in-out,
        box-shadow 0.15s ease-in-out;
}

